.container {
  align-items: center;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  margin: 0;
}

.description {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 12px;
  width: 500px;
  margin: 0;

  @media (max-width: 768px) {
    width: 300px;
  }

  p {
    font-weight: 400;
    font-size: 110%;
  }
}